.contacts {
  margin: 0 auto;
  margin-top: 120px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 1160px;
  column-gap: 100px;

  @media (max-width: 1160px) {
    width: 1004px;
  }

  @include vp-1023 {
    width: 678px;
    column-gap: 0;
    margin-top: 102px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 11px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @include vp-374 {
    width: 320px;
    padding: 0;
  }
}

.contacts__map {
  margin: 0 auto;
  width: 560px;

  @include vp-1023 {
    width: 324px;
    height: 335px;
  }

  @include vp-767 {
    order: 2;
    width: 100%;
  }

  @include vp-374 {
    width: 290px;
  }
}

.contacts__container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  @include vp-1023 {
    padding-left: 15px;
  }

  @include vp-767 {
    order: 1;
    align-items: center;
    padding-left: 0;
  }
}

.contacts__container h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin: 0;
  margin-top: 77px;
  margin-bottom: 32px;

  @include vp-1023 {
    margin-top: 62px;
    margin-bottom: 26px;
  }

  @include vp-767 {
    margin-top: 47px;
    text-align: center;
    width: 100%;
  }

  @include vp-374 {
    text-align: left;
    width: 286px;
  }
}

.contacts__container p {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  padding-left: 30px;
  margin: 0;
  margin-bottom: 20px;
  padding-right: 35px;

  @include vp-767 {
    padding-left: 44px;
    padding-right: 0;
  }

  &::before {
    position: absolute;
    content: "";
    width: 18px;
    height: 22px;
    background-image: url("../img/svg/baloon.svg");
    background-repeat: no-repeat;
    top: 0;
    left: 0;

    @include vp-1023 {
      top: 4px;
    }

    @include vp-767 {
      top: 6px;
      left: 13px;
    }
  }
}

.contacts__phone {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  padding-left: 30px;
  margin: 0;
  margin-bottom: 20px;

  &::before {
    position: absolute;
    content: "";
    width: 18px;
    height: 22px;
    background-image: url("../img/svg/phone.svg");
    background-repeat: no-repeat;
    top: 0;
    left: 0;
  }

  @include vp-767 {
    width: 290px;
    margin-bottom: 33px;
  }
}
